import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "src/environments/environment";

@Injectable()
export class ItensController {
  private api: string = environment.api;

  constructor(private Http: HttpClient) {}

  async getItensPendentes() {
    return await this.Http.get(`${this.api}/itens`)
      .toPromise()
      .then((data) => {
        return data;
      });
  }

  async pickItem(idPessoa: number, idItem: number) {
    return await this.Http.put(`${this.api}/itens/pick`, { id_pessoa: idPessoa, id_item: idItem }, {responseType: 'text'})
      .toPromise()
      .then((data) => {
        return data;
      });
  }
}
