import { Component, OnInit } from '@angular/core';
import { ItensController } from './controllers/itens.controller';
import { PessoasController } from './controllers/pessoas.controller';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  pessoas: any;
  pessoa: any = null;

  itens: any;
  item: any = null;

  saved: any = false;

  constructor(private pessoasController: PessoasController, private itensController: ItensController) {}

  ngOnInit() {
    this.getPessoas();
    this.getItens();
  }

  async getPessoas() {
    this.pessoas = await this.pessoasController.getPessoasPendentes();
  }

  async getItens() {
    this.itens = await this.itensController.getItensPendentes();
  }

  async selectPresente() {
    try {
      console.log(`A pessoa ${this.pessoa} escolheu o presente: ${this.item}.`);
      const resp = await this.itensController.pickItem(this.pessoa, this.item);
      this.saved = true;
    } catch (error) {
      console.log(error);
    }
  }
}
